import React from "react";
import "./Card.css";
import { AiOutlineCode } from "react-icons/ai";

export default function Card({
  isActive,
  icon,
  text,
  textColor,
  labelText,
  labelTextColor,
}) {
  return (
    <div
      className="Card"
      style={
        isActive
          ? {
              backgroundColor: "#676CDB",
              height: "30.5rem",
              width: "21rem",
              boxShadow: "0 2px 4px 4px #4a4ea130",
            }
          : { backgroundColor: "#29313D" }
      }
    >
      {icon}
      <h2 style={{ color: textColor }}>{text}</h2>
      <p style={{ color: labelTextColor }}>{labelText}</p>
    </div>
  );
}

Card.defaultProps = {
  isActive: false,
  icon: <AiOutlineCode style={{ color: "#676CDB", transform: "scale(2)" }} />,
  text: "Use the attributes",
  textColor: "#ffffff",
  labelTextColor: "#d1d1d1",
};
