import React from "react";
import "./index.css";
import {
  AiOutlineCopyrightCircle,
  AiFillHeart,
  AiOutlineArrowUp,
} from "react-icons/ai";
import { BsDot } from "react-icons/bs";
import Logo from "../Reusable/Logo";
import ContactList from "../Reusable/ContactList";

export default function Footer() {
  return (
    <div className="Footer">
      <div className="Footer__Top">
        <Logo className="Logo" />
        <ContactList className="ContactList" />
      </div>
      <div className="Footer__Bottom">
        <p>
          Copyright
          <AiOutlineCopyrightCircle color="#ffffff" />
          2021
          <BsDot color="#ffffff" />
          Made with <AiFillHeart color="#ffffff" /> by Avi Avinav
        </p>
      </div>
      {/* <button className="Footer__Scroller">
        <AiOutlineArrowUp color="#ffffff" />
      </button> */}
    </div>
  );
}
