import React from "react";
import Card from "../Reusable/Card";
import "./index.css";
import {
  AiOutlineCode,
  AiOutlineAppstore,
  AiOutlineAntDesign,
} from "react-icons/ai";
import { BsArrow90DegDown } from "react-icons/bs";

export default function Skills() {
  return (
    <div className="Experience">
      <h1>
        Skills
        <BsArrow90DegDown
          color="#676cdb"
          style={{ transform: "scale(1.6) rotateY(180deg)" }}
        />
      </h1>
      <div className="Experience__Cards">
        <Card
          icon={
            <AiOutlineAntDesign
              style={{ color: "#676CDB", transform: "scale(2)" }}
            />
          }
          text={"UI / UX Designer."}
          labelText={"Figma, AdobeXD, Canva"}
        />
        <Card
          isActive={true}
          icon={
            <AiOutlineCode
              style={{ color: "#ffffff", transform: "scale(2)" }}
            />
          }
          text={"Web Developer."}
          labelText={"React.js, Next.js, Express, APIs, Node, HTML, CSS, JS"}
          textColor={"#ffffff"}
        />
        <Card
          icon={
            <AiOutlineAppstore
              style={{ color: "#676CDB", transform: "scale(2)" }}
            />
          }
          text={"App Developer."}
          labelText={"React Native, Java(Android), Cross-Platform"}
        />
      </div>
    </div>
  );
}
