import React, { useState, useEffect } from "react";
import "./ProjectCard.css";

export default function ProjectCard({
  flexDirection,
  projectName,
  projectDesc,
  imgUrl,
  imgWidth,
  projectUrl,
  arrIcons,
}) {
  const [iconsArr, setIconsArr] = useState([]);
  useEffect(() => {
    setIconsArr([arrIcons]);
  }, [arrIcons]);
  return (
    <a
      href={projectUrl}
      target="_blank"
      rel="noreferrer"
      className="ProjectCard"
      style={{ flexDirection: flexDirection }}
    >
      <img
        src={imgUrl}
        alt=""
        style={{ width: window.innerWidth <= 767 ? "150%" : imgWidth }}
      />
      <div className="ProjectCard__Text">
        <h1>{projectName}</h1>
        <p>{projectDesc}</p>
        <div>
          {iconsArr.map(() => {
            return iconsArr[iconsArr.length - 1];
          })}
        </div>
      </div>
    </a>
  );
}
