import React, { useState, useEffect } from "react";
import "./index.css";
import Logo from "../Reusable/Logo";
import { Link } from "react-scroll";
import { ImCross } from "react-icons/im";

export default function Navbar() {
  const [scrolled, setScrolled] = useState(false);
  const [toggled, setToggled] = useState(false);
  useEffect(() => {
    window.onscroll = () => {
      if (window.innerWidth >= 767) {
        if (
          document.body.scrollTop >= 100 ||
          document.documentElement.scrollTop >= 100
        ) {
          setScrolled(true);
        } else if (
          document.body.scrollTop <= 100 ||
          document.documentElement.scrollTop <= 100
        ) {
          setScrolled(false);
        }
      }
    };
  }, []);
  return (
    <nav
      className="Navbar"
      style={
        scrolled
          ? { background: "#222A36", boxShadow: "0 2px 4px 6px #00000030" }
          : { background: "transparent" }
      }
    >
      <button className="Nav-Toggler" onClick={() => setToggled(!toggled)}>
        <span></span>
        <span></span>
        <span></span>
      </button>
      <ul
        className="Nav-List"
        style={toggled ? { display: "flex" } : { display: "none" }}
      >
        <li>
          <Link
            activeClass="active"
            to="Hero"
            spy={true}
            smooth={true}
            offset={-70}
            duration={500}
          >
            Me
          </Link>
        </li>
        <li>
          <Link
            activeClass="active"
            to="Experience"
            spy={true}
            smooth={true}
            offset={60}
            duration={500}
          >
            Skills
          </Link>
        </li>
        <li>
          <Link
            activeClass="active"
            to="Projects"
            spy={true}
            smooth={true}
            offset={60}
            duration={500}
          >
            Projects
          </Link>
        </li>
      </ul>
      {/* <ul
        className="Nav-Links-Mobile"
        style={
          window.innerWidth <= 767
            ? toggled
              ? { display: "flex" }
              : { display: "none" }
            : { display: "none" }
        }
      >
        <li>
          <Link
            activeClass="active"
            to="Hero"
            spy={true}
            smooth={true}
            offset={-70}
            duration={500}
          >
            Me
          </Link>
        </li>
        <li>
          <Link
            activeClass="active"
            to="Experience"
            spy={true}
            smooth={true}
            offset={60}
            duration={500}
          >
            Skills
          </Link>
        </li>
        <li>
          <Link
            activeClass="active"
            to="Projects"
            spy={true}
            smooth={true}
            offset={60}
            duration={500}
          >
            Projects
          </Link>
        </li>
      </ul> */}
      <div className="Nav-Name">
        <Logo />
      </div>
    </nav>
  );
}
