import React from "react";
import ProjectCard from "../Reusable/ProjectCard";
import "./index.css";
import {
  SiStyledComponents,
  SiReact,
  SiSpotify,
  SiNodeDotJs,
} from "react-icons/si";
import { BsArrow90DegDown } from "react-icons/bs";
import NetflixClone from "../../assets/images/NetflixClone.png";
import SpotifyClone from "../../assets/images/SpotifyClone.png";
import AviCodeEditor from "../../assets/images/AviCodeEditor.png";
import Aloria from "../../assets/images/Aloria.svg";

export default function Projects() {
  let flexD = window.innerWidth <= 767 ? "column" : "row";
  let flexDR = window.innerWidth <= 767 ? "column" : "row-reverse";

  return (
    <div className="Projects">
      <h1>
        <BsArrow90DegDown color="#676cdb" style={{ transform: "scale(1.6)" }} />
        Projects
      </h1>
      <ProjectCard
        flexDirection={flexD}
        projectName="Netlix Clone App"
        projectDesc="A clone of the netflix (frontend ui) created 
        with react native and styled components. It is compatible with both iOS and Android."
        imgUrl={NetflixClone}
        imgWidth="35%"
        projectUrl="https://github.com/AviAvinav/Netflix-Clone"
        arrIcons={[
          <SiReact color="#676cdb" />,
          <SiStyledComponents size={28} color="#676cdb" />,
        ]}
      />
      <ProjectCard
        flexDirection={flexDR}
        projectName="Aloria UI"
        projectDesc="An open-source free-to-use react library to use amazing components, hooks, fonts and icons."
        imgUrl={Aloria}
        imgWidth="62%"
        projectUrl="https://github.com/AviAvinav/aloria-ui"
        arrIcons={[
          <SiReact color="#676cdb" />,
          <SiNodeDotJs color="#676cdb" />,
        ]}
      />
      <ProjectCard
        flexDirection={flexD}
        projectName="Avi's Code Editor"
        projectDesc="A simple online code editor made with react and codemirror. The code editor
         lets you write code and gives a live preview."
        imgUrl={AviCodeEditor}
        imgWidth="60%"
        projectUrl="https://github.com/AviAvinav/Avi-Code-Editor"
        arrIcons={[
          <SiReact color="#676cdb" />,
          <SiNodeDotJs color="#676cdb" />,
        ]}
      />
      <ProjectCard
        flexDirection={flexDR}
        projectName="Spotify Clone"
        projectDesc="A clone of spotify made using the spotify api and react."
        imgUrl={SpotifyClone}
        imgWidth="55%"
        projectUrl="https://github.com/AviAvinav/React-Spotify-Clone"
        arrIcons={[
          <SiReact color="#676cdb" />,
          <SiSpotify color="#676cdb" />,
          <SiNodeDotJs color="#676cdb" />,
        ]}
      />
    </div>
  );
}
